/* TYPOGRAPHY */
h1 {
  font-family: "Space Grotesk", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  margin-top: 0;
}
h1 {
  font-size: 44px;
}
@media screen and (min-width: 768px) {
  h1 {
    font-size: calc(
          44px + 8 *
            ((100vw - 768px) / 432)
        );
  }
}
@media screen and (min-width: 1200px) {
  h1 {
    font-size: 52px;
  }
}

h2 {
  font-family: "Space Grotesk", sans-serif;
  font-weight: 500;
  text-transform: uppercase;
  margin: 0;
}
h2 {
  font-size: 33px;
}
@media screen and (min-width: 768px) {
  h2 {
    font-size: calc(
          33px + 7 *
            ((100vw - 768px) / 432)
        );
  }
}
@media screen and (min-width: 1200px) {
  h2 {
    font-size: 40px;
  }
}

p {
  font-family: "Space Grotesk", sans-serif;
  font-weight: 400;
  line-height: 135%;
}
p {
  font-size: 16px;
}
@media screen and (min-width: 768px) {
  p {
    font-size: calc(
          16px + 2 *
            ((100vw - 768px) / 432)
        );
  }
}
@media screen and (min-width: 1200px) {
  p {
    font-size: 18px;
  }
}

