html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

html {
  overflow-x: hidden;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  overflow-x: hidden;
  padding: 0;
  display: flex;
  flex-flow: column;
  min-height: calc(100vh - 30px);
}
@media (min-width: 992px) {
  body {
    min-height: 100vh;
  }
}

* {
  -webkit-tap-highlight-color: transparent;
}

.c-header {
  background-color: black;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 2%;
  padding-right: 2%;
}
.c-header .c-logo {
  display: flex;
}
@media (max-width: 767.9px) {
  .c-header .c-logo {
    margin-bottom: 5px;
    justify-content: center;
    flex-wrap: wrap;
  }
}
.c-header .c-nav {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  list-style: none;
}
@media (max-width: 767.9px) {
  .c-header .c-nav {
    justify-content: space-around;
    flex-wrap: wrap;
  }
}
.c-header .c-nav__list {
  display: flex;
  list-style-type: none;
  padding: 0;
  gap: 3.5vw;
}
.c-header .c-nav__list_item {
  text-transform: uppercase;
  font-family: "Space Grotesk", sans-serif;
}
.c-header .c-nav__list_item {
  font-size: 17px;
}
@media screen and (min-width: 768px) {
  .c-header .c-nav__list_item {
    font-size: calc(
          17px + 1 *
            ((100vw - 768px) / 432)
        );
  }
}
@media screen and (min-width: 1200px) {
  .c-header .c-nav__list_item {
    font-size: 18px;
  }
}
.c-header .c-nav__list_item a {
  color: white;
  text-decoration: none;
}

.c-footer {
  background-color: black;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 2%;
  padding-right: 2%;
  color: white;
}
.c-footer .site-title {
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  color: white;
  text-decoration: none;
}
@media (max-width: 767.9px) {
  .c-footer .site-title {
    justify-content: center;
  }
  .c-footer .site-title p {
    margin-bottom: 0;
  }
}
.c-footer .c-nav {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  list-style: none;
}
@media (max-width: 767.9px) {
  .c-footer .c-nav {
    justify-content: center;
    flex-wrap: wrap;
  }
}
.c-footer .c-nav__list {
  display: flex;
  list-style-type: none;
  padding: 0;
  gap: 25px;
}
.c-footer .c-nav__list_item {
  margin: auto;
}
.c-footer .c-nav__list_item a {
  color: white;
  text-decoration: none;
  transition: opacity 0.1s ease-in-out;
}
.c-footer .c-nav__list_item a:hover {
  transition: opacity 0.1s ease-in-out;
  opacity: 0.6;
}

.c-hero {
  margin-top: 2rem;
  border: 1px solid black;
  position: relative;
}
.c-hero__content {
  padding: 2%;
  padding-left: 5%;
}
.c-hero__content::before {
  content: "";
  display: block;
  position: absolute;
  top: 5%;
  left: 0;
  width: 15px;
  height: 70%;
  background: url("assets/images/hero-decoration.svg") no-repeat center center;
  background-size: cover;
  z-index: -1;
}
@media (max-width: 991.9px) {
  .c-hero__content {
    padding: 5%;
    padding-top: 45px;
  }
  .c-hero__content::before {
    top: 0;
    left: 5%;
    width: 60%;
    height: 15px;
    background: url("assets/images/hero-decoration-mobile.svg") no-repeat center center;
    background-size: 100% auto;
  }
}

.c-card {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.c-card__img {
  display: block;
  height: 270px;
}
.c-card__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.c-card__content {
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: auto;
  display: flex;
  flex-direction: column;
  padding: 5%;
  border: 1px solid black;
}
.c-card__content__title {
  padding-bottom: 15px;
  position: relative;
}
.c-card__content__title::after {
  content: "";
  display: block;
  position: relative;
  top: 10px;
  width: 70%;
  height: 5px;
}
.c-card__content__title--red::after {
  background: #a62e19;
}
.c-card__content__title--blue::after {
  background: #4265a8;
}
.c-card__content__title--green::after {
  background: #3e883e;
}
.c-card__content p {
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: auto;
}
.c-card__content__arrow {
  padding-top: 2rem;
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.plyr iframe {
  transition: 0.2s filter linear;
}

.plyr.plyr--paused iframe {
  filter: blur(1.5rem);
}

.main-video {
  height: 48vh;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 1s;
  opacity: 0;
}
.main-video--loaded {
  opacity: 1;
}
.main-video--ready {
  height: 70vh;
  transition: 1s;
  background-color: black;
}
.main-video--ready #player::before, .main-video--ready #player::after {
  transition: 1s;
  position: absolute;
  content: "";
  top: 0;
  bottom: 0;
  width: 5%;
  z-index: 2;
  pointer-events: none;
}
.main-video--ready #player::before {
  left: 0;
  background: linear-gradient(270deg, rgba(0, 0, 0, 0) 0%, black 100%);
}
.main-video--ready #player::after {
  right: 0;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, black 100%);
}
.main-video #player {
  width: 100vw;
  max-width: 1800px;
}
@media (max-width: 1023.9px) {
  .main-video {
    height: 25vh;
  }
  .main-video--ready {
    height: 40vh;
    transition: 1s;
    background-color: black;
  }
  .main-video #player {
    transform: scale(1.7);
    min-height: 100% !important;
  }
}
@media (max-width: 767.9px) {
  .main-video {
    height: 15vh;
  }
  .main-video--ready {
    height: 40vh;
    transition: 1s;
    background-color: black;
  }
  .main-video #player {
    transform: scale(1.7);
    min-height: 100% !important;
  }
}